      klineParameter: { a1: [], a2: [], a3: [], a4: [], a5: [], b1: [], b2: [], b3: [], b4: [], b5: [] },
      zsData: { data: [{ name: '--' }, { name: '--' }, { name: '--' }, { name: '--' }, { name: '--' }] },
      xs: [], // x轴集合
      datay1: [],
      datay2: [],
      datay3: [],
      zg: '',
      zd: '',
      zs: '',
      kd: '',
      timer: '',
      keycd: null
    }
  },
  beforeDestroy() {
    clearInterval(this.timer)
    this.timer = null
    document.getElementById('quick-macro').removeEventListener('keydown', this.keycd)
  },
  methods: {
    getTimer() {
      this.getHttpData()
      this.getTableData()
      this.getZSData()
      this.getKlineParameterData()
    },
    getTableData() {
      this.$http
        .get(this.baseUrl + '/stock_info/get_time_sharing_data/num=' + this.stockID + '/')
        .then(
          response => {
            this.tableData = response.body.data
            this.$nextTick(() => {
              this.$refs.table.bodyWrapper.scrollTop = this.$refs.table.bodyWrapper.scrollHeight
            })
            console.log('连接成功' + this.$refs.table)
          },
          response => {
            console.log('连接出错Table')
          }
        )
        .catch(function(response) {
          console.log('连接Table-catch')
        })
    },
    getZSData() {
      var url = this.baseUrl + '/stock_info/get_zs/'
      this.$http
        .get(url)
        .then(
          response => {
            console.log('连接成功')
            this.zsData = response.body
          },
          response => {
            console.log('连接出错Kline')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    getKlineParameterData() {
      var url = this.baseUrl + '/stock_info/get_k_line/num=' + this.stockID + '/'
      var selfThis = this
      this.$http
        .get(url)
        .then(
          response => {
            response.body.amount = this.getText(response.body.amount)
            response.body.volume_index = this.getText(response.body.volume_index)
            response.body.main_outflow = this.getText(response.body.main_outflow)
            response.body.main_inflow = this.getText(response.body.main_inflow)

            selfThis.$refs.inflowsDiv.style.width = response.body.main_inflow_ratio * 100 + '%'
            selfThis.$refs.outOfDiv.style.width = response.body.main_outflow_ratio * 100 + '%'
            this.klineParameter = response.body
            console.log('连接成功')
          },
          response => {
            console.log('连接出错Klinep')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    getText(text) {
      var str = text.toString().split('.')
      if (str[0].length > 8) {
        return str[0].substring(0, str[0].length - 8) + '.' + str[0].substring(str[0].length - 8, str[0].length - 6) + '亿'
      } else if (str[0].length > 4) {
        return str[0].substring(0, str[0].length - 4) + '万'
      } else {
        return text
      }
    },
    getHttpData() {
      this.$http
        .get(this.baseUrl + '/stock_info/get_time_sharing_chart/num=' + this.stockID + '/')
        .then(
          response => {
            var responseXS = []
            var responseData1 = []
            var responseData2 = []
            var responseData3 = []
            this.xs = ''
            for (var i = 0; i < response.body.data.length; i++) {
              responseXS.push(response.body.data[i].time)
              responseData1.push(response.body.data[i].t_line)
              responseData2.push(response.body.data[i].average_line)
              responseData3.push(response.body.data[i].t_vol)
            }
            this.xs = responseXS
            this.datay1 = responseData1
            this.datay2 = responseData2
            this.datay3 = responseData3
            // this.xs.push(response.body.data[i].time)
            // this.datay1.push(response.body.data[i].t_line)
            // this.datay2.push(response.body.data[i].average_line)
            // this.datay3.push(response.body.data[i].t_vol)
            this.zd = (parseFloat(response.body.low).toFixed(2))
            this.zg = (parseFloat(response.body.high).toFixed(2))
            this.zs = (parseFloat(response.body.pre_close).toFixed(2))

            if ((this.zg - this.zs) - (this.zs - this.zd)) {
              this.kd = this.zg - this.zs
            } else {
              this.kd = this.zs - this.zd
            }
            this.kd = (parseFloat(this.kd).toFixed(2))
            this.drawCjl()
            console.log('连接成功')
          },
          response => {
            console.log('连接出错dema')
          }
        )
        .catch(function(response) {
          console.log('连接catch' + response)
        })
    },
    tableRowStyle({ row, rowIndex }) {
      return 'background-color: black; border: 0'
    },
    getRandom(m, n) {
      return Math.floor(Math.random() * (n + 1 - m) + m)
    },
    mock() {
      const xydata = []
      const data1 = [2801, 2801.12, 2802.43, 2803.54, 2805.65, 2808.16, 2809.47, 2811.88, 2812.99, 2833.22, 2824.55, 2812.22, 2814.55, 2817.99, 2820.22]
      const data2 = [0.12, 0.38, 0.43, 0.66, 0.77, 0.89, 1.19, 1.32, 1.38, 1.42, 2.48, 1.55, 1.59, 1.77, 1.88]
      Array(60)
        .fill(null)
        .forEach((data, index) => {
          const time = index < 10 ? `13:0${index}` : `13:${index}`
          xydata.push({
            time: time,
            data1: this.getRandom(2800, 2830),
            data2: this.getRandom(1, 2),
            data3: this.getRandom(300, 10000)
          })
        })
      Array(60)
        .fill(null)
        .forEach((data, index) => {
          const time = index < 10 ? `14:0${index}` : `14:${index}`
          xydata.push({
            time: time,
            data1: this.getRandom(2800, 2830),
            data2: this.getRandom(1, 2),
            data3: this.getRandom(300, 10000)
          })
        })

      this.xs = xydata.map(data => data.time)
      this.xs.push('15:00')

      this.datay1 = data1
      this.datay2 = data2
      this.datay3 = xydata.map(data => data.data3)
      console.log(this.datay3)
    },
    /* 成交量柱状图 */
    drawCjl() {
      const left = this.$echarts.init(document.getElementById('left'))
      // const dzs = this.zs
      // const dzg = this.zg
      // const dzd = this.zd
      // const dkd = this.kd
      const dzs = parseFloat(this.zs).toFixed(2)
      const dkd = parseFloat(this.kd).toFixed(2)
      const dzd = parseFloat(dzs - dkd).toFixed(2)
      const dzg = parseFloat(this.zg).toFixed(2)
      console.log('获取 this.zs = ' + dzs)
      console.log('获取 this.zg = ' + dzg)
      console.log('获取 this.zd = ' + dzd)
      console.log('获取 this.kd = ' + dkd)

      const option = {
        grid: [{ x: '5%', y: '4%', width: '90%', height: '55%' },
          { x: '5%', y2: '2%', width: '90%', height: '39%' }],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        xAxis: [
          {
            type: 'category',
            axisTick: {
              show: false
            },
            gridIndex: 0,
            axisLine: {
              fontSize: 10,
              lineStyle: {
                width: 1,
                color: '#CE181E'
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ['#CE181E'],
                width: 1,
                type: 'solid'
              }
            }
          },
          {
            type: 'category',
            gridIndex: 1,
            axisLine: {
              lineStyle: {
                color: '#CE181E'
              }
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              interval: 59
            },
            data: this.xs
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#fff',
              padding: [50, 0, 0, 10]
            },
            axisTick: {
              show: false
            },
            min: dzd,
            max: dzg,
            scale: false,
            interval: dkd * 2 / 14,
            axisLine: {
              lineStyle: {
                color: '#CE181E'
              }
            },
            axisLabel: {
              formatter(value) {
                var num = parseFloat(value).toFixed(2)
                var num2 = parseFloat(dkd / 7)
                var texts = []
                // console.log(value)
                if (num === parseFloat(dzs - num2 * 7).toFixed(2)) {
                  texts.push('')
                } else {
                  texts.push(num)
                }
                return texts
              },
              fontSize: 10,
              show: true,
              textStyle: {
                color(value) {
                  var num = parseFloat(value).toFixed(2)
                  var num2 = parseFloat(dzs).toFixed(2)
                  if (num > num2) {
                    return '#B00004'
                  }
                  if (num === num2) {
                    return '#f3f3f3'
                  }
                  if (num < num2) {
                    return '#4BFFFF'
                  }
                }
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ['#CE181E']
              }
            }
          },
          {
            type: 'value',
            axisTick: {
              show: false
            },
            nameTextStyle: {
              color: '#fff',
              padding: [0, 0, 0, 80]
            },
            scale: false,
            gridIndex: 0,
            axisLine: {
              lineStyle: {
                color: '#CE181E'
              }
            },
            min: dzd,
            max: dzg,
            interval: dkd * 2 / 14,
            axisLabel: {
              fontSize: 10,
              show: true,

              textStyle: {
                color(value) {
                  var num = parseFloat(value).toFixed(2)
                  var num2 = parseFloat(dzs).toFixed(2)
                  if (num > num2) {
                    return '#B00004'
                  }
                  if (num === num2) {
                    return '#f3f3f3'
                  }
                  if (num < num2) {
                    return '#4BFFFF'
                  }
                }
              },
              formatter(value) {
                var num = parseFloat(value).toFixed(2)
                var num2 = parseFloat(dkd / 7)
                var texts = []
                if (num === parseFloat(dzs - num2 * 7).toFixed(2)) {
                  texts.push('')
                } else if (num === parseFloat(dzs - num2 * 6).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 6 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(dzs - num2 * 5).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 5 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(dzs - num2 * 4).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 4 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(dzs - num2 * 3).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 3 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(dzs - num2 * 2).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 2 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(dzs - num2 * 1).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 1 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(dzs).toFixed(2)) {
                  texts.push('0.00' + '%')
                } else if (num === parseFloat(parseFloat(dzs) + num2 * 1).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 1 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(parseFloat(dzs) + num2 * 2).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 2 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(parseFloat(dzs) + num2 * 3).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 3 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(parseFloat(dzs) + num2 * 4).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 4 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(parseFloat(dzs) + num2 * 5).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 5 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(parseFloat(dzs) + num2 * 6).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 6 * 100).toFixed(2) + '%')
                } else if (num === parseFloat(parseFloat(dzs) + num2 * 7).toFixed(2)) {
                  texts.push(parseFloat((dkd / dzs) / 7 * 7 * 100).toFixed(2) + '%')
                }
                return texts
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ['#CE181E']
              }
            }
          },
          {
            type: 'value',
            axisTick: {
              show: false
            },
            scale: true,
            gridIndex: 1,
            axisLine: {
              lineStyle: {
                color: '#CE181E'
              }
            },
            axisLabel: {
              fontSize: 10,
              show: true,
              textStyle: {
                color: '#FFFF00'
              },
              formatter(value) {
                return value
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: '#CE181E'
              }
            }
          },
          {
            type: 'value',
            axisTick: {
              show: false
            },
            scale: true,
            gridIndex: 1,
            splitNumber: 5,
            axisLine: {
              lineStyle: {
                color: '#CE181E'
              }
            },
            axisLabel: {
              fontSize: 10,
              show: true,
              textStyle: {
                color: '#FFFF00'
              },
              formatter(value) {
                return value
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ['#CE181E']
              }
            }
          }
        ],
        series: [
          {
            data: this.datay1,
            type: 'line',
            symbol: 'none',
            xAxisIndex: 0,
            yAxisIndex: 0,
            itemStyle: {
              color: '#fff'
            },
            markLine: {
              lineStyle: {
                color: '#CE181E'
              }
            },
            lineStyle: {
              width: 1.5
            }
          },
          {
            data: this.datay2,
            type: 'line',
            symbol: 'none',
            xAxisIndex: 0,
            yAxisIndex: 0,
            itemStyle: {
              color: '#FFFF00'
            },
            markLine: {
              lineStyle: {
                color: '#CE181E'
              }
            },
            lineStyle: {
              width: 1.5
            }
          },
          {
            data: this.datay3,
            type: 'bar',
            barWidth: 1,
            xAxisIndex: 1,
            yAxisIndex: 2,
            itemStyle: {
              color: '#FFFF0B'
            }
          },
          {
            data: this.datay3,
            type: 'bar',
            barWidth: 1,
            xAxisIndex: 1,
            yAxisIndex: 3,
            itemStyle: {
              color: '#111111'
            }
          }
        ]
      }
      left.setOption(option)
      window.onresize = left.resize
    }
  }
}
</script>
